@import 'mixins.scss';

//region Colors

$blue : #03a9f4 !default;
$deep-orange : #ff5722 !default;
$orange : #ff9800 !default;
$amber : #ffc107 !default;
$indigo : #3f51b5 !default;
$lime : #cddc39 !default;
$teal : #009688 !default;
$green : #4caf50 !default;
$light-green : #8bc34a !default;
$purple : #9c27b0 !default;
$yellow : #ffeb3b !default;
$red : #f44336 !default;
$dark-gray : #505057 !default;
$gray : #b0b0b7 !default;
$light-gray : #e0e0e7 !default;

$darkest : #000 !default;
$lightest : #fff !default;

$main-color : $blue !default;
$secondary-color : $orange !default;

//endregion

//region Widgets

$widget-font-weight : 300 !default;

$widget-highlight-color : $secondary-color !default;
$widget-text-bright-color : $lightest !default;
$widget-text-dark-color : $darkest !default;
$widget-border-color : #777 !default;
$widget-border-radius : 2px !default;
$widget-border-width : 2px !default;

$widget-anchor-width : 16px !default;
$widget-anchor-height : 8px !default;

$widget-badge-color : $widget-text-bright-color !default;
$widget-badge-background-color : $red !default;

$float-box-shadow : 3px 3px 6px rgba(0, 0, 0, 0.4) !default;

$button-color : $widget-text-bright-color !default;
$button-disabled-color : $dark-gray !default;
$button-disabled-background-color : $light-gray !default;

$checkbox-color : $widget-text-bright-color !default;

$container-color : $widget-text-bright-color !default;

$datepicker-background-color : #555 !default;
$datepicker-color : $widget-text-bright-color !default;
$datepicker-month-color : $widget-text-bright-color !default;

$field-color : $darkest !default;
$field-background-color : $lightest !default;
$field-invalid-border-color : $red !default;
$error-tip-color : $red !default;
$error-tip-background-color : lighten(#FFFDE7, 3)!default;

$popup-background-color : $dark-gray !default;
$popup-color : $widget-text-bright-color !default;
$popup-header-background-color : #03a9f4!default;
$popup-header-color : #fff !default;

$menu-color : $widget-text-bright-color !default;
$menu-background-color : lighten($popup-background-color, 5) !default;
$menu-item-border-color : lighten($popup-background-color, 15) !default;
$menu-item-separator-color : lighten($menu-item-border-color, 10) !default;
$menu-highlight-color : $main-color !default;

$list-color : $widget-text-bright-color !default;
$list-background-color : lighten($popup-background-color, 5) !default;
$list-item-border-color : lighten($popup-background-color, 15) !default;
$list-item-separator-color : $list-item-border-color !default;
$list-highlight-color : $main-color !default;

$slider-thumb-color : $main-color !default;
$slider-track-color : $light-gray !default;
$slider-thumb-radius : 10px !default;
$slider-track-height : 8px !default;
$slider-contrast : 40% !default;
$slider-margin : $slider-thumb-radius 0 !default;

$tabpanel-hover-background-color : darken($popup-background-color, 5) !default;
$tabpanel-tab-text-color : $widget-text-bright-color !default;
$tabpanel-tab-hover-color : $widget-text-bright-color !default;
$tabpanel-background-color : darken($popup-background-color, 5) !default;
$tabpanel-border-color : $widget-border-color !default;
$tabpanel-border-width : 2px !default;

$toast-background-color : $popup-background-color !default;
$toast-color : $widget-text-bright-color !default;
$toast-progress-color : $main-color !default;

$tooltip-color : $widget-text-bright-color !default;
$tooltip-background-color : lighten($popup-background-color, 5) !default;

$mask-background-color : $main-color !default;
$mask-color : $widget-text-bright-color !default;

//endregion

//region Grid

$grid-row-height : 45px !default;

$grid-text-brightest-color : $lightest !default;
$grid-text-darkest-color : $darkest !default;

$grid-row-odd-color : lighten($light-gray, 8) !default;
$grid-row-even-color : #fff !default;

$grid-selection-color : $secondary-color !default;

$grid-background-color : transparent !default;
$grid-highlight-color : $secondary-color !default;
$grid-red-color : $red !default;

$grid-cell-focused-outline-color : transparentize($main-color, .5) !default;
$grid-cell-focused-outline-width : 2px !default;
$grid-cell-focused-outline-style : solid !default;
$grid-cell-focused-background-color : null !default;
$grid-cell-focused-border-radius : 0 !default;

$grid-cell-color : $grid-text-darkest-color !default;
$grid-cell-border-color : $light-gray !default;
$grid-cell-font-weight : 300 !default;
$grid-cell-hover-background-color : fade-out($grid-selection-color, 0.9) !default;
$grid-cell-row-hover-background-color : fade-out($grid-selection-color, 0.95) !default;

$grid-row-selected-background-color : fade-out($grid-selection-color, 0.95) !default;
$grid-row-selected-cell-background-color : fade-out($grid-selection-color, 0.85) !default;
$grid-focus-row-selected-background-color : fade-out($grid-selection-color, 0.9) !default;
$grid-focus-row-selected-cell-background-color : fade-out($grid-selection-color, 0.85) !default;

$grid-footer-background-color : $light-gray !default;

//endregion

//region Header

$grid-header-background-color : $light-gray !default;
$grid-header-hover-background-color : lighten($grid-header-background-color, 5) !default;
$grid-header-border-color : $gray !default;
$grid-header-invalid-drag-color : $red !default;
$grid-header-container-border-bottom-width : 1px !default;

$grid-header-resizing-background-color : $grid-header-hover-background-color !default;

$grid-header-icon-target : '' !default;

$grid-header-icon-color : $gray !default;
$grid-header-icon-light-color : darken($grid-header-background-color, 5) !default;
$grid-header-icon-highlight-color : $secondary-color !default;
$grid-header-resizer-handle-width : 10px !default;
$grid-header-touch-resizer-handle-width : 20px !default;

$grid-header-font-weight : inherit !default;
$grid-header-text-color : $grid-text-darkest-color !default;

$grid-header-filtered-color : #666 !default;

//region region resize
$grid-splitter-width : 1px !default;
$grid-region-resize-splitter-width : 5px !default;
$grid-region-resize-splitter-color : lighten($grid-header-border-color, 10) !default;
$grid-region-resize-splitter-narrow-color : darken($grid-header-border-color, 10) !default;
$grid-region-resize-splitter-width-touch : 25px !default;
//endregion

//endregion

//region Columns

$percent-bar-fill : $main-color !default;
$percent-bar-low-fill : $red !default;
$percent-bar-color : $widget-text-bright-color !default;
$percent-bar-background : #f1f1f1 !default;

$rownumber-background : #f1f1f1 !default;
$rownumber-border-color : $grid-cell-border-color !default;

$rating-empty-color : transparentize($light-gray, .7) !default;
$rating-filled-color : lighten($amber, 10) !default;

//endregion

$quickfind-background-color : lighten($yellow, 20) !default;
$quickfind-badge-color : darken($yellow, 30) !default;
$quickfind-header-badge-color : $quickfind-badge-color !default;
$quickfind-font-weight : bold !default;

$grid-group-header-color : $dark-gray !default;
$grid-group-header-background-color : #fff !default;
$grid-group-header-line-color : $grid-header-border-color !default;
$grid-group-header-line-width : 2px !default;
$grid-group-header-icon-color : $dark-gray !default;
$grid-group-column-header-icon-color : $grid-header-icon-highlight-color !default;
$grid-groupsummary-background-color : lighten($light-gray, 8) !default;

$sort-index-color : $grid-text-brightest-color !default;

$columndragtoolbar-background : $popup-background-color !default;
$columndragtoolbar-box-shadow : none !default;
$columndragtoolbar-border-radius : 1em !default;
$columndragtoolbar-opacity : .4 !default;
$columndragtoolbar-hover-opacity : .8 !default;
$columndragtoolbar-default-color : $main-color !default;
$columndragtoolbar-default-hover-color : $columndragtoolbar-default-color !default;
$columndragtoolbar-icon-color : $main-color !default;
$columndragtoolbar-secondary-color : $secondary-color !default;
$columndragtoolbar-secondary-hover-color : $columndragtoolbar-secondary-color !default;
$columndragtoolbar-secondary-background-color : transparent !default;
$columndragtoolbar-button-border-width : .3em !default;
$columndragtoolbar-button-background-color : transparent !default;
$columndragtoolbar-title-color : #fff !default;
$columndragtoolbar-button-box-shadow : none !default;
$columndragtoolbar-button-hover-box-shadow : none !default;

@mixin b-grid-header-icon() {
    font-family : $icon-font;
    color       : $grid-header-icon-color;
    position    : absolute;
    transition  : transform 0.3s, color 0.2s;
    top         : 50%; // needed for safari
    transform   : translateY(-50%); // needed for safari
}
